/* src/game.css */
/* game.css */
.game-container, .game-area {
    margin: 0;
    padding: 0;
    width: 100vw;
    height: 100vh;
    overflow: hidden;
  }
  
  .game-container {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    background-image: url('gameObtacles/background.gif');
    background-repeat: no-repeat;
    background-size: cover;
  }
  
  .game-area {
    position: relative;
    width: 100%;
    height: 100%;
  }
  
  .scoreboard {
    position: absolute;
    top: 10px;
    left: 10px;
    color: white;
    font-size: 24px;
  }
  
  .game-over {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: white;
    text-align: center;
  }
  @media (max-width: 600px) {
    html body {
        overflow: hidden;
        justify-content: center;
        align-items: center;
        touch-action: none;
    }
    .scoreboard {
      font-size: 16px;
      z-index: 200000;
    }
    .controll{
        justify-content: center;
        justify-items: center;
        display: flex;
        flex-direction: column;
        height: auto;
        width: 100%;
        background-color: #000000;
    }
    .game-container .game-area{
        background-image: url('gameObtacles/background.gif');
        background-repeat: no-repeat;
        background-size: cover;
        overflow-x: hidden;
        overflow-y: hidden;
        width: 100%;
        height: 100%;
        touch-action: none;
        position: absolute;
    }
  
    .game-over {
      font-size: 18px;
      flex-direction: column;
    }
    .game-over button {
        width: 50px;
        height: 50px;
    }
    .full-screen1 {
        overflow: hidden;
        height: 100%;
        width: 100%;
        position: fixed;
        justify-content: center;
        align-items: center;
      }
    .firstAdd{
        height: 100px;
    }
    .secondAdd{
        width: 100px;
        justify-content: center;
        align-items: center;
    }
    .infos{
        height: auto;
        padding-bottom: 3cm;
        flex-wrap: wrap;
        font-size: 12px;
        margin-left: -3%;
        width: 300px;
    }
    .infos p{
        font-size: 10px;
        flex-wrap: wrap;
        font-style: italic;
        height: auto;
        width: 300px;
    }
  }